import React, { Component } from 'react'
import $ from 'jquery'
import url from './audio/gbqq.mp3' // 引入背景音乐文件


class Main extends Component {
    state = {
        date: {},
    }
    componentDidMount() {
        this.print();
        setInterval(() => {
            this.time(2019, 10, 1) // 你们的纪念日
        }, 1000
        )
        var audio = document.getElementById("audio");
        setTimeout(() => audio.play(), 8500) // 背景音乐在页面加载后，延迟播放的时长，单位：毫秒。
    }
    print = () => {
        $.fn.autotype = function () {
            var _this = $(this);
            var str = _this.html();
            // 正则替换代码行之间添加的多个空格，不去除换行输出会有明显的停顿：实际是在输出多个空格
            str = str.replace(/(\s){2,}/g, "$1");
            var index = 0;
            $(this).html('');
            var timer = function fn() {
                var args = arguments;
                var current = str.slice(index, index + 1);
                // html标签完整输出,如：<p>
                if (current == '<') {
                    index = str.indexOf('>', index) + 1;
                }
                else {
                    index++;
                }
                //位运算符: 根据setInterval运行奇偶次来判断是否加入下划线字符“_”，使输入效果更逼真
                if (index < str.length - 1) { //打印字符倒数第2个字符开始，不加下划线字符，以防止结束符可能会多输出一下划线字符
                    _this.html(str.substring(0, index) + (index & 1 ? '_' : ''));
                } else {
                    _this.html(str.substring(0, index));
                    clearTimeout(timer);
                };
                setTimeout(fn, 200)
            };
            // 延迟1s开始
            setTimeout(timer, 1000);
        };
        $("#autotype").autotype();
    }
    time = (year, month, day) => {
        var dateNow = new Date();
        var dateJNR = new Date(year, month - 1, day);
        // var anniversary = parseInt((dateNow - dateJNR) / (365*24*3600*1000))
        var d = parseInt((dateNow - dateJNR) / (24 * 3600 * 1000));
        var hour = parseInt(((dateNow - dateJNR) / (3600 * 1000)) % 24);
        var minute = parseInt((dateNow - dateJNR) / (1000 * 60) % 60);
        var second = parseInt((dateNow - dateJNR) / 1000 % 60);
        this.setState({ date: { d: d, hour: hour, minute: minute, second: second } });
    };
    render() {
        const date = () => {
            if (this.state.date.d !== undefined) {
                const { d, hour, minute, second } = this.state.date
                return (<p>我们已经一起走过了: <span className="date-text">{d}</span> 天 <span className="date-text">{hour}</span> 小时 <span className="date-text">{minute}</span> 分 <span className="date-text">{second}</span> 秒 </p>
                )
            }
        }
        return (
            <div className="App animated bounceInLeft">
                <div className="date">{date()}</div>
                <div id="autotype">
                    <h1 style={{ fontWeight: 900 }}>晚上好！Zoey！</h1>
                    <p >在煽情即将开始之前，先放首歌当背景音乐吧！Music!</p>
                    <p>今天是我们一周年的纪念日，从2019年10月01日到现在，我们一起经历了太多的事情，
                    有欢笑也有争吵，也总是因为一些事情闹过分手，但是我们都走过来了。</p>
                    <p>你还记得嘛，在伦敦那会儿，我总是想去Scape蹭饭，每天都期待着见到你。买好果汁和肉肉，帮你和睿睿做饭，这是family的美好回忆；
						在正课那会儿，我总是期待着去AL见你，陪你一起学习，陪你一起做饭，陪你一起生活；
						后面隔离的时候，我们搬了很多次的家，每一次都会把我们累的半死，当时的生活很难，很辛苦，每天提心吊胆，杀菌消毒。
						但是，那时候我真的很快乐。真的。每次想起来，我想笑，想哭，不知道我们还能不能回去了。
                </p>
                    <p>或许你不知道，这一段经历对我的意义是什么，从那时候开始，我发自内心地把你当做家人一样看待。
						在我的心目中，虽然这只是短短一年的时间，但是我们经历的事情仿佛过了一个世纪。
						我真的很喜欢你，发自内心的喜欢，虽然有时候我们会吵得很凶，我会说的很过分。
						但是，我没有一次是发自内心的想分开。
						我知道我的脾气有时候臭的很，你也为了我做了很多，改变了很多。我全部都看在眼里，记在心里。
                </p>
                    <p>隔离的时候，我总是想偷偷去隔壁看你，因为真的很想你，每天的拿饭时间是我一天中最快乐的时候。
						因为，我能看你一眼。
						现在，异地已经一个月过去了。我很想你，很想很想，想到不知道用什么词语来描述。
						我怀念在Scape的日子，在AL的日子，在豪宅的日子，在LP的日子。
						每一秒钟，都显得那么珍贵。
                </p>
                    <p>未来的一年，我最大的愿望就是你能找到伦敦的工作，我能顺利拿到全奖。
						我想和你一起生活，一起去看外面更广阔的世界。
                </p>
                    <p>最后祝我的女孩，以梦为马，聘天下。华羽为服，栖梧桐树，坚韧为翼，长空万里。付出甘之若饴，所得归于欢喜。</p>
                    <div style={{ textAlign: 'right' }}>
                        <p>爱你的♥炫紫藤</p>
                        <p>2020年9月30日晚</p>
                    </div>
                </div>
                <audio id="audio" src={url}></audio>
            </div>

        )
    }
}
export default Main


